import { message } from 'antd'
import storage from '@/utils/storage'


export default function asyncApi ({ url, method, data = {}, headers, success, fail, doHideAlert }) {
  const token = storage.get('token')
  if (token) {
    data.token = token
  }
  const baseUrl = '/api'
  let fetchObj = {}
  if (method) {
    fetchObj = {
      method,
      body: JSON.stringify(data || {}),
      headers: new Headers({
        ...headers,
        'content-type': 'application/json'
      })
    }
  }


  return new Promise((resolve,reject) => {
    fetch(baseUrl + url, fetchObj).then(res => {
      console.log(res)
      // console.log(res.json())
      if (res.status === 200) {
        let json = res.json()
        json.then(response => {
          console.log(response)
          if (response.code !== 0) {
            message.error(response.message)
            reject()
            if (response.code === 7000 || response.code === 7001) {
              storage.clear('token')
              if (this) {
                this.props.dispatch({type: 'CLEAR_TOKEN'})
                this.props.history.push('/')
              }
            }
          } else {
            resolve(response)
          }
        })

      } else {
        message.error('网络错误或服务器异常，请稍后再试！')
        reject()
      }
    })
  })

}

