import React, { Component } from 'react'
import { Layout, Menu, Icon, Avatar, Dropdown, Breadcrumb } from 'antd'
import { connect } from 'react-redux'
// import asyncApi from '@/utils/asyncApi'

import storage from '@/utils/storage'

import {
  Route,
  Switch,
  Link,
  withRouter,
} from 'react-router-dom'

import Find from './Find'
import Index from './Index'
import Edit from './Edit'
import Picture from './Picture'
import Ligoudan from './Ligoudan'
import Panel from './panel/Panel'

const { Header, Sider, Content } = Layout

class Home extends Component {

  state = {
    collapsed: false,
    sync: true,
    selectedKeys: []
  }

  componentWillMount() {
    console.log(this.props)
    this.setState({
      selectedKeys: [this.props.location.pathname]
    })
  }


  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    })
  }

  sync = () => {
    this.setState({
      sync: false
    })
    setTimeout(() => {
      this.setState({
        sync: true
      })
    },300)
  }

  menuClick = (index, e) => {
    console.log(e)
    console.log(index)
    this.props.history.push(e.key)
    this.setState({
      selectedKeys: [e.key]
    })
  }

  breadcrumbItemsMenuClick = (index, e) => {
    this.props.history.push(index)
    this.setState({
      selectedKeys: [index]
    })
  }

  logout() {
    storage.clear('token')
    this.props.CLEAR_TOKEN()
    this.props.history.push('/')
  }



  render() {

    const NoMatch = ({ location }) => (
      <div>
        <h3>
          还未开发 <code>{location.pathname}</code>
        </h3>
      </div>
    )

    const menu = (
      <Menu>
        <Menu.Item key="0" onClick={() => this.logout()}>
          退出登录<Icon type="export"/>
        </Menu.Item>
      </Menu>
    )

    const breadcrumbNameMap = {
      '/': 'LIGOUDAN',
      '/file': '文档管理',
      '/Picture': '图片管理',
      '/Edit': '编辑器',
      '/Find': '接口调用测试',
      '/Index': 'redux测试',
      '/panel': '面板',
    }

    const { location } = this.props
    const pathSnippets = location.pathname.split('/').filter(i => i)
    const extraBreadcrumbItems = pathSnippets.map((_, index) => {
      const url = `/${pathSnippets.slice(0, index + 1).join('/')}`
      return (
        <Breadcrumb.Item key={url}>
          <Link to={url}>
            {breadcrumbNameMap[url]}
          </Link>
        </Breadcrumb.Item>
      )
    })

    const breadcrumbItems = [(
      <Breadcrumb.Item key="home">
        <span className='cur_p' onClick={(e) => this.breadcrumbItemsMenuClick('/', e)}>LIGOUDAN</span>
      </Breadcrumb.Item>
    )].concat(extraBreadcrumbItems)

    return (
      <Layout>
        <Sider
          style={{
            overflow: 'auto',
            height: '100vh',
            position: 'fixed',
            left: 0,
          }}
          trigger={null}
          collapsible
          collapsed={this.state.collapsed}
        >
          <div className="logo">
            {
              this.state.collapsed ? 'LGD' : 'LIGOUDAN'
            }
          </div>
          <Menu theme="dark" mode="inline" selectedKeys={this.state.selectedKeys} >

            <Menu.Item key="/" onClick={(e) => this.menuClick(0, e)}>
              <Icon type="home" />
              <span>LIGOUDAN</span>
            </Menu.Item>

            <Menu.Item key="/file" onClick={(e) => this.menuClick(0, e)}>
              <Icon type="file-text"/>
              <span>文档管理</span>
            </Menu.Item>

            <Menu.Item key="/panel" onClick={(e) => this.menuClick(0, e)}>
              <Icon type="file-text"/>
              <span>面板</span>
            </Menu.Item>

            <Menu.Item key="/Picture" onClick={(e) => this.menuClick(0, e)}>
              <Icon type="picture" />
              <span>图片管理</span>
            </Menu.Item>

            <Menu.Item key="/Edit" onClick={(e) => this.menuClick(0, e)}>
              <Icon type="edit"/>
              <span>编辑器</span>
            </Menu.Item>

            <Menu.Item key="/Find" onClick={(e) => this.menuClick(0, e)}>
              <Icon type="swap"/>
              <span>接口调用测试</span>
            </Menu.Item>

            <Menu.Item key="/Index" onClick={(e) => this.menuClick('home', e)}>
              <Icon type="smile"/>
              <span>redux测试</span>
            </Menu.Item>

          </Menu>
        </Sider>

        <Layout className="animation" style={{ marginLeft: this.state.collapsed ? 80 : 200 }}>
          <Header className="animation" style={{
            background: '#fff',
            padding: 0,
            left: this.state.collapsed ? 80 : 200
          }}>
            <Icon
              className="trigger"
              type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
              onClick={this.toggle}
            />

            <Icon
              className= {'icon_aync trigger ' + (this.state.sync ? '' :'active')}
              type='sync'
              onClick={this.sync}
            />
            <div style={{ 'float': 'left' }}>

            </div>
            <div style={{
              'float': 'right',
              'paddingRight': '20px'
            }} className='userName'>
              <Dropdown overlay={menu} trigger={['click']}>
                <div className="ant-dropdown-link" href="#">
                  <span className='name'>{this.props.user.userName}</span>
                  <Avatar icon="user"/>
                </div>
              </Dropdown>

            </div>
          </Header>
          <Content style={{
            margin: '0 20px 20px 20px',
          }}
          >
            <div style={{
              margin: '20px 0',

            }}>
              <Breadcrumb>
                {breadcrumbItems}
              </Breadcrumb>
            </div>
            <div style={{
              background: '#fff',
              padding: '24px',
            }}>
	            {
	            	this.state.sync ? (
			            <Switch>
				            <Route exact path='/' component={Ligoudan}/>
				            <Route path='/Find' component={Find}/>
				            <Route path='/Index' component={Index}/>
				            <Route path='/Edit' component={Edit}/>
				            <Route path='/Picture' component={Picture}/>
				            <Route path='/Panel' component={Panel} />
				            <Route component={NoMatch}/>
			            </Switch>
		            ) : null
	            }

            </div>
          </Content>
        </Layout>
      </Layout>
    )
  }
}


function mapStateToProps(state) {
  if (state.login.token) console.log(state)
  return {
    user: state.login.user,
    token: state.login.token
  }
}

function mapDispatchToProps(dispatch) {
  return {
    CLEAR_TOKEN: () => dispatch({
      type: 'CLEAR_TOKEN'
    }),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Home))