const tokenState = {
  token: '',
  user: {}
}

const login = (state = tokenState, action) => {
  switch (action.type) {
    case 'SET_TOKEN':
      console.log(action.token)
      return {
        user: state.user,
        token: action.token
      }
    case 'SET_USER':
      return {
        user:action.user,
        token: state.token
      }
    case 'CLEAR_TOKEN':
      console.log(action.token)
      return {
        token: ''
      }
    default:
      return state
  }
}

export default login