import React, { Component } from 'react'
import { connect } from 'react-redux'
import asyncApi from '@/utils/asyncApi'


import storage from '@/utils/storage'

import './css/layout.scss'
import 'antd/dist/antd.css'


import {
  HashRouter as Router,
  Route,
  Redirect,
  Switch
} from 'react-router-dom'


import Login from '@/pages/Login'

import Home from '@/pages/Home'



class App extends Component {
  componentWillMount() {
    let token = storage.get('token')
    this.props.SET_TOKEN(token)
    if (token) {
      asyncApi({
      method: 'post',
      url: '/getUserByToken'
      }).then(res => {
        console.log(res)
        this.props.SET_USER(res.data)
      })
    }

  }


  render() {
    return (
        <div id="components-layout-demo-custom-trigger">
          <Router >
            <Switch>
              <Route path="/login" component={Login} />
              <Route
                path="/"
                render={props => {
                  if (storage.get('token') || this.props.token) {
                    return <Home {...props} />
                  } else {
                    return <Redirect to="/login" />
                  }
              }}/>
            </Switch>
          </Router>
        </div>
    )
  }
}


function mapStateToProps(state) {
  if (state.login.token) console.log(state)
  return {
    token: state.login.token
  }
}

function mapDispatchToProps(dispatch) {
  return {
    SET_TOKEN: (token) => dispatch({
      type: 'SET_TOKEN',
      token: token
    }),
    SET_USER: (user) => dispatch({
        type: 'SET_USER',
        user: user
    })
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App)
