import React, { Component } from 'react'
import {Checkbox, Col, Row, Skeleton} from 'antd'
import asyncApi from '@/utils/asyncApi'
import { connect } from 'react-redux'

import Masonry from 'react-masonry-component';

const masonryOptions = {
  transitionDuration: 0
};

const imagesLoadedOptions = { background: '.my-bg-image-el' }

const CheckboxGroup = Checkbox.Group

const plainOptions = ['风景', '代码', '人物', '设计图']
const defaultCheckedList = ['风景', '代码', '人物', '设计图']


class Picture extends Component {

  state = {
    checkedList: defaultCheckedList,
    indeterminate: false,
    items: [],
    checkAll: true,
  }

  onChange = (checkedList) => {
    this.setState({
      checkedList,
      indeterminate: !!checkedList.length && (checkedList.length < plainOptions.length),
      checkAll: checkedList.length === plainOptions.length,
    })
  }

  onCheckAllChange = (e) => {
    this.setState({
      checkedList: e.target.checked ? plainOptions : [],
      indeterminate: false,
      checkAll: e.target.checked,
    })
  }

  getUser() {
    asyncApi.call(this, {
      url: '/users',
      method: 'post',
      loding: true,
      data: {}
    })
  }


  componentWillMount() {
    this.getImage()
  }

  getImage () {
    asyncApi.call(this, {
      url: '/getImg',
      method: 'post',
      loding: true,
      data: {}
    }).then(res => {
      console.log(res.data)
      this.setState({
        items: res.data
      })
    })
  }




  render() {

    const childElements = this.state.items.map((element,index) =>{
      return (
        <Col xs={12} sm={12} md={8} lg={6} xl={4} xxl={3} key={index}>
          <div  className="image-element-class">
            <img src={'http://192.168.1.133:9989/image/' + element.url} alt='' />
          </div>
        </Col>
      );
    });

    const imagesLoadedOptions = { background: '.my-bg-image-el' }


    return (
      <div className='Picture'>

        <div className='Checkbox'>
          <Checkbox
            indeterminate={this.state.indeterminate}
            onChange={this.onCheckAllChange}
            checked={this.state.checkAll}
          >
            全选
          </Checkbox>
          <CheckboxGroup options={plainOptions} value={this.state.checkedList} onChange={this.onChange}/>
        </div>


        <div>
          {
            this.state.items.length ? (
              <Row gutter={8}>
                <Masonry
                  className={'my-gallery-class'} // default ''
                  elementType={'div'} // default 'div'
                  options={masonryOptions} // default {}
                  disableImagesLoaded={false} // default false
                  updateOnEachImageLoad={false} // default false and works only if disableImagesLoaded is false
                  imagesLoadedOptions={imagesLoadedOptions} // default {}
                >
                  {childElements}
                </Masonry>
              </Row>
            ) : (
              <Skeleton active />
            )
          }


        </div>

      </div>
    )
  }
}

export default connect()(Picture)