import React, { Component } from 'react'
// import {
//   BrowserRouter as Router,
//   Route,
// } from 'react-router-dom'
//
// import Home from './Home'

import { connect } from 'react-redux'


class Index extends Component {

  componentWillMount() {

  }

  apiTest(e) {
    console.log(e)
  }


  render() {
    return (
      <div>
        <button onClick={ (e) =>  this.apiTest(e)}>99988888</button>
        <button onClick={(e) => this.props.Add('dsfsd')}>点11击我</button>
        {
          this.props.test.map((item, index) => <div key={index}> {item} </div>)

        }
        {/*<Router>*/}

        {/*	<Top/>*/}
        {/*	<Route exact path='/home' component={Home}/>*/}
        {/*</Router>*/}
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    test: state.test
  }
}

function mapDispatchToProps(dispatch) {
  return {
    Add: (text) => dispatch({
      type: 'ADD',
      text: text
    }),
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Index)