import React, {Component} from 'react'
import asyncApi from '@/utils/asyncApi'
import storage from '@/utils/storage'

import {
	withRouter,
} from 'react-router-dom'

import {connect} from 'react-redux'

import {
	Form, Icon, Input, Button, Checkbox, message
} from 'antd';
import md5 from "md5";


class Register extends Component {

	data = {
		token: ''
	}

	state = {
		loading: false,
		register: false
	}

	componentWillMount() {
		console.log(this.props)
	}

	handleSubmitRegister = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			if (!err) {
				console.log(e)
				console.log(values)
				if (values.passWord !== values.passWord2) {
					message.error('两次密码输入不一样，请重新输入！')
				} else {
					if (this.state.loading) return
					this.setState({loading: true})
					asyncApi({
						url: '/register',
						method: 'post',
						loding: true,
						data: {
							userName: values.userName,
							passWord: values.passWord
						}
					}).then(res => {
						this.setState({loading: false})
						message.success('注册成功，请登录！')
						this.login()
					}).catch(res => {
						this.setState({loading: false})
					})
				}
			}
		});
	}



	//注册

	login() {
		this.props.history.push('/login')
	}



	render() {

		const {getFieldDecorator} = this.props.form;

		return (

			<div>

				<div className='login_from ' id='components-form-demo-normal-login'>
					<Form onSubmit={this.handleSubmitRegister} className="login-form">
						<Form.Item>
							{getFieldDecorator('userName', {
								rules: [{required: true, message: '请输入你的username!'},{min: 6, message: 'username长度过短!'}],
							})(
								<Input prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)'}}/>} placeholder="Username"/>
							)}
						</Form.Item>

						<Form.Item>
							{getFieldDecorator('passWord', {
								rules: [{required: true, message: '请输入你的Password!'},{min: 6, message: 'Password长度过短!'}],
							})(
								<Input prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>} type="password"
								       placeholder="Password"/>
							)}
						</Form.Item>

						<Form.Item>
							{getFieldDecorator('passWord2', {
								rules: [{required: true, message: '请重复输入你的Password!'},{min: 6, message: 'Password长度过短!'}],
							})(
								<Input prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>} type="password"
								       placeholder="repetition Password"/>
							)}
						</Form.Item>


						<Form.Item>

							<Button type="primary" htmlType="submit" className="login-form-button">
								注册
							</Button>
							<span onClick={() => this.login()} className='cur_p' style={{color: '#1890ff'}}>登录</span>
						</Form.Item>
					</Form>
				</div>
			</div>
		)
	}
}

function mapStateToProps(state) {
	return {}
}

function mapDispatchToProps(dispatch) {
	return {
		SET_TOKEN: (token) => dispatch({
			type: 'SET_TOKEN',
			token: token
		}),
	}
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form.create({name: 'normal_Register'})(Register)));